a {
  color: rgba(255, 255, 255, 0); }

.project-text {
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.6rem;
  text-align: justify;
  line-height: 1.3;
  margin: 0% 10%; }
  @media screen and (orientation: landscape) {
    .project-text {
      margin: 0%; } }
  @media screen and (orientation: landscape) and (min-width: 1200px) {
    .project-text {
      text-align: center; } }

.project-Buttons {
  display: flex;
  margin: 0 10%;
  justify-content: space-between; }
  @media (min-width: 1200px) {
    .project-Buttons {
      justify-content: space-around; } }

.single {
  justify-content: space-around; }

.btn-out {
  width: 45%;
  height: 3rem;
  display: flex;
  align-items: center;
  border-left: 1px solid rgba(255, 255, 255, 0.089);
  border-right: 1px solid rgba(255, 255, 255, 0.089);
  cursor: pointer;
  user-select: none;
  transition: 1s border ease; }
  @media (min-width: 1200px) {
    .btn-out {
      width: 30%;
      height: 5rem; }
      .btn-out:hover {
        border-top: 1px solid rgba(255, 255, 255, 0.089);
        border-bottom: 1px solid rgba(255, 255, 255, 0.089); } }
  .btn-out > p {
    width: 100%;
    text-align: center;
    font-family: "BenchNine", sans-serif;
    text-transform: uppercase;
    letter-spacing: 1rem;
    font-size: 1.6rem;
    line-height: 1.5;
    color: rgba(255, 255, 255, 0.788);
    padding-left: 1rem; }
    @media (min-width: 1200px) {
      .btn-out > p {
        transition: 0.2s color  ease; }
        .btn-out > p:hover {
          color: rgba(255, 255, 255, 0.945); } }

.project-container {
  grid-row: 1/2;
  grid-column: 1/3;
  align-self: center;
  min-height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: space-around; }
  @media screen and (orientation: landscape) {
    .project-container {
      grid-column: 2/3;
      border: none; } }
  @media screen and (orientation: landscape) and (min-width: 900px) {
    .project-container {
      min-height: 80%;
      padding: 3% 0%;
      grid-column: 2/3;
      border-top: 1px solid rgba(255, 255, 255, 0.069);
      border-bottom: 1px solid rgba(255, 255, 255, 0.069); } }

.skills {
  align-items: center; }
  @media screen and (orientation: landscape) and (min-width: 900px) {
    .skills {
      flex-direction: row;
      align-items: flex-start; } }

.fade-appear {
  opacity: 0.01; }

.fade-appear-active {
  opacity: 1;
  transition: opacity 1000ms ease-in; }

.fade-exit {
  opacity: 1;
  transition: opacity 1000ms ease-in; }

.fade-exit-active {
  opacity: 0;
  transition: opacity 1500ms ease-in; }

.fade-enter {
  opacity: 0.01;
  transition: opacity 1000ms ease-in; }

.fade-enter-active {
  opacity: 1;
  transition: opacity 1000ms ease-in; }

.App {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 20;
  color: white;
  display: grid;
  grid-template-rows: 15% 75% 10%;
  grid-template-columns: 100%;
  overflow: hidden; }

header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  z-index: 40;
  cursor: pointer;
  user-select: none;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.466) 0%, rgba(0, 0, 0, 0.116) 100%); }

.logo {
  font-family: "BenchNine", sans-serif;
  color: rgba(255, 255, 255, 0.938);
  text-align: center;
  text-transform: uppercase;
  font-weight: 300; }

.heading-name {
  letter-spacing: 3px;
  font-size: 3rem;
  line-height: 1;
  margin: 0; }

.heading-frontend {
  letter-spacing: 5px;
  font-size: 1.3rem; }

main {
  position: relative;
  z-index: 40;
  background-color: rgba(0, 0, 0, 0.116); }

nav {
  display: flex;
  position: relative;
  z-index: 40;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.233) 0%, rgba(0, 0, 0, 0.116) 100%); }

.SwitchButton {
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  font-size: 1.5rem;
  color: rgba(255, 255, 255, 0.918);
  letter-spacing: 2px;
  cursor: pointer;
  position: relative; }
  .SwitchButton::after {
    content: '';
    bottom: 0;
    position: absolute;
    left: 0;
    width: 100%;
    height: 1px;
    background: rgba(255, 255, 255, 0.185);
    opacity: 0;
    transition: 1s all ease; }
  @media (min-width: 1200px) {
    .SwitchButton:hover::after {
      opacity: 1; }
    .SwitchButton:active::after {
      background: rgba(255, 255, 255, 0.507); } }

.videoContainer {
  position: relative;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.678); }

video {
  z-index: -2;
  position: absolute;
  width: auto;
  height: auto;
  min-width: 100%;
  min-height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }
